.section {
    background-color: white;
    margin: 10px 0;
    box-shadow: 0 0 5px #ddd;
}

.section__header {
    border-bottom: #ddd solid 1px;
    display: grid;
    grid-template-columns: auto auto;
    grid-gap: 20px;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

@media(max-width: 767px){
    .section__header {
        grid-template-columns: 1fr;
    }
}

.section__title{
font-size:20px;
margin: 0;
}

.section__body {
    padding: 20px;
}

.section__footer {
    margin-top: 25px;
    text-align: center;
    font-size: 15px;
}